@import url(https://fonts.googleapis.com/css2?family=Creepster&family=Monofett&family=Roboto:wght@300&family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* get font from https://fonts.google.com/ */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  font: 100% 'Roboto', sans-serif;
}

button {
  text-transform: uppercase;
}

/* for same style */
.welcome,
.notes,
.score {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 10%);
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

/* for welcome class */
.welcome img {
  animation-name: moves;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}

.welcome h1 {
  font-family: 'Creepster', cursive;
  color: seagreen;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 10%);
}

@keyframes moves {
  0% {
    transform: rotate(0deg);
  } 50% {
    transform: rotate(1deg);
  } 100% {
    transform: rotate(-1deg);
  }
}
/* end of welcome class */


/* for notes class */
.notes {
  padding: 10px;
}

.notes h1 {
  font-size: 3rem;
  text-decoration: underline;
}

.notes > p {
  font-size: 1.8rem;
  padding: 30px 10px;
  margin: 20px 0;
  background-color: royalblue;
  color: whitesmoke;
  border-radius: 5px;
}

.notes > button {
  padding: 8px 14px;
  border-radius: 5px;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  background-color: springgreen;
  border: 1px solid lightgreen;
  color: #333;
  font-size: 1rem;
}

.notes-box {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.boxes {
  width: 80%;
  background-color: gray;
  position: relative;
  padding: 30px;
  animation-name: popUpTop;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(0.91, -0.22, 0.82, 0.57);
}

.boxes {
  font-size: 1.6rem;
}

.box-hides {
  display: none;
}

@keyframes popUpTop {
  from {
    transform: translateY(-100%);
  } to {
    transform: translateY(0%);
  }
}

.boxes button {
  background-color: crimson;
  color: whitesmoke;
  border: none;
  outline: none;
  padding: 4px;
  border-radius: 100%;
  position: absolute;
  top: -8px;
  right: -8px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  font-size: medium;
}
/* end for notes class */


/* for question class */
.question {
  width: 100%;
  height: 100vh;
  font-size: 1rem;
  padding: 30px;
  display: grid;
  justify-content: center;
  justify-items: center;
}

.question > img {
  border: 1px solid black;
  padding: 3px;
  border-radius: 5px;
  max-width: 100%;
}

.question form {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.question form label {
  grid-column: 1/2;
}

.question form label:nth-child(1) {
  grid-column: 1/-1;
}

.question form input[type="radio"] {
  accent-color: red;
  margin-right: 10px;
}

.question form input[type="submit"] {
  grid-column: 2/3;
  justify-self: end;
  align-self: flex-end;
  cursor: pointer;
  background-color: dodgerblue;
  border: 1px solid navy;
  padding: 8px 17px;
  border-radius: 4px;
  text-transform: uppercase;
}

.score-info {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 10px;
}

.score-info p {
  font-family: 'VT323', monospace;
  margin-right: 10px;
}

.score-info img {
  width: 25px;
  height: 25px;
}
/* end of question class */

/* for score class */
.score h1 {
  font-family: 'Monofett', cursive;
  font-size: 3rem;
}

.score p {
  font-size: 1.8rem;
  text-align: center;
}

.score button {
  background-color: dodgerblue;
  padding: 8px 15px;
  border: 1px solid navy;
  border-radius: 3px;
  cursor: pointer;
  text-transform: uppercase;
}
/* end of score class */

/* not support for small screen */
@media only screen 
and (max-width: 320px) 
and (max-height: 568px) {
  body,
  .welcome,
  .notes,
  .question,
  .score {
    display: none;
  }

  body::after {
    content: 'not support for small devices!';
    color: red;
  }
}
